html {
  height: 100%;
}

body {
  width: 100%;
  // height: 100%;

  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: $ff-ptmono;
  font-weight: 400;
  font-size: $fz-default;
  line-height: 21px;
  color: $color-default-black;
  background-color: $color-default-white;
}

a {
  color: $color-default-black;
  text-decoration: none;
}

img {
  // display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

textarea {
  resize: none;
}

